'use client';
import styles from '../css/cart.module.css';

interface CartItem {
    id: string;
    name: string;
    price: number;
    image: string;
    quantity: number;
}

interface CartListProps {
    items: CartItem[];
}

export default function CartList({ items }: CartListProps) {
    if (items.length === 0) {
        return <p>Giỏ hàng trống</p>;
    }

    return (
        <div className={styles.cartList}>
            {items.map(item => (
                <div key={item.id} className={styles.cartItem}>
                    <img 
                        src={item.image} 
                        alt={item.name} 
                        className={styles.itemImage}
                        onError={(e) => {
                            (e.target as HTMLImageElement).src = '/fallback.jpg'; // ảnh fallback nếu lỗi
                        }}
                    />
                    <div className={styles.itemInfo}>
                        <h3>{item.name}</h3>
                        <p>Giá: <strong>{item.price.toLocaleString('vi-VN')} VNĐ</strong></p>
                        <p>Số lượng: <strong>{item.quantity}</strong></p>
                        <p>Tổng: <strong>{(item.price * item.quantity).toLocaleString('vi-VN')} VNĐ</strong></p>
                    </div>
                </div>
            ))}
        </div>
    );
}
